.SwearJar {
    background-color: #ffffff;
    border-radius: 16px;
    width: 80vw;
    height: 80vh;
    margin-top: 4vh;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 7px 11px 20px 0px rgba(0,0,0,0.75);
    padding: 16px;
    box-sizing: border-box;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
}